body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}
img{
  max-width: 100%;
}
.floatRight{
  float:right;
}

.banner{
    width:100%;
    height:80vh;
    background-image: linear-gradient(rgba(0,0,0,0.8),rgba(0,0,0,0.8)),
     url(/static/media/banner.078b203c.jpg);
     background-size: cover;
     background-position: center;
     background-repeat: no-repeat;
     position: relative;
}
.bannerText{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: #e0e3e6;
    text-align: center;
}
.bannerText p{
    width: 60%;
    margin:15px auto 30px;
}
.contact{
    padding: 50px 0;
    margin: 40px 0 0;
   background: rebeccapurple;
}
.contactBtn{
    float: right;
}
.footer{
    width: 100%;
    padding: 40px 0;
    background-image: linear-gradient(rgba(0,0,0,0.9),rgba(0,0,0,0.9)),
    url(/static/media/banner.078b203c.jpg);
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    position: relative;
}
.footer ul{
    list-style:none;
}
.footer ul li a, .footer p{
    text-decoration: none;
    color: #a7a7a7;
}
.footer h4{
    color: #a7a7a7;
}
.footer ul li a{
    transition: .5s;
}
.footer ul li a:hover{
    color:red;
}
.footer ul li{
    margin-bottom:5px;
}
.social ul{
    text-align: left;
    margin-top: 15px;
}
.social ul li{
    margin-right: 15px;
    font-size: 25px;
}

.footer h4{
    margin-left: 30px;
    margin-bottom: 15px;
}
.recentWork{
    background: url(/static/media/recetwork-bg.bd7949ad.jpg)no-repeat;
    background-size: cover;
    background-position: center;
    margin:50px 0;
    padding:35px 0;
    color: aliceblue;
    text-align: center;
}
.serviceTitle{
    padding:40px 0;
    text-align: center;
}
.serviceTitle h3{
    text-transform: capitalize;
    position: relative;
    padding-bottom:4px;
    color:#7a3cbd;;
}
.serviceTitle h3::after{
    position: absolute;
    content: "";
    width:5%;
    height:4px; 
    top: 100%;
    left:45%;
    background:#b684eb;
    display: block;
}
.imageContainer img{
   transition: .5s;
}

.imageContainer img:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}
.card{
    overflow: hidden;
}
.testiImg{
    width: 100px;
    height:100px;
    border-radius:50%;
}
.testimonial p{
    color: #aeadaf;
}
.navbar ul{
    padding-top:5px;
}
.navbar ul li{
    margin-left: 20px;
}
.navbar ul li .nav-link,
.navbar ul li a{
    color: aliceblue!important;
    text-transform: uppercase;
    font-size:15px;
}
.navbar ul li a{
    text-decoration: none;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0;
}
.header{
    background-image: linear-gradient(to bottom, #c15cd3c2, rgb(173, 132, 240));
}
.logo{
    width: 100%;
}
.logo img{
    height:50px;
}
.sidebar{
    width:100%;
    height:100vh;
    background-color: #747474;
    padding-top:10px;
}
.sidebar ul li{
    list-style: none;
    margin-bottom:15px; 
}
.sidebar ul li a{
    text-decoration: none;
    color: #e9e5e5;
}
.welcomeText{
    position: absolute;
    top:40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
